import styled from "styled-components";

const Wrapper = styled.div`
  * {
    transition: all 0.4s;
  }
  padding: 64px 24px;
  width: 100vw;
  box-sizing: border-box;
  & h2 {
    width: 100%;
    text-align: center;
    color: #e74c3c;
    font-weight: bold;
  }
`;
export default Wrapper;
